import request from '@/utils/request'

export function getList(query) {
  return request({
    url: 'catalog/list',
    method: 'get',
    params: query,
    mock: false
  })
}
// 创建提醒
export function createRemind(data) {
  return request({
    url: 'notify',
    method: 'post',
    data,
    mock: false
  })
}
// 是否需要提醒
export function trialRemind(params) {
  return request({
    url: 'notify',
    method: 'get',
    params,
    mock: false
  })
}
