<template>
  <!-- 我的错题 -->
  <div class="wrong-questions">
    <template v-if="questionList.length">
      <h3 class="title">
        西安图灵网络精神文化（企业精神、企业价值、经营哲学）
      </h3>
      <div v-for="(item, idx) of questionList" :key="idx" class="question">
        <p class="stem">
          <span
            >{{ idx + 1 }}.<span
              style="display: inline-block;"
              v-html="item.stem"
          /></span>
          <span class="operation">
            <span class="pass" @click="delQuestion(item._id)">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#iconqianshoushenpitongguo" />
              </svg>
              pass
            </span>
            <span class="review" @click="review(idx)">
              <svg class="icon" aria-hidden="true">
                <use xlink:href="#icondanju" />
              </svg>
              去复习
            </span>
          </span>
        </p>
        <el-checkbox-group
          v-if="item.type === 'checkbox'"
          size="small"
          :disabled="true"
        >
          <el-checkbox
            v-for="option of item.standard_options"
            :key="option.index"
            :label="option.index"
            :class="getResultColor(option.index, idx)"
            @change="setOptions(option.index, idx, item._id)"
          >
            <svg
              v-if="getResultColor(option.index, idx) === 'error'"
              class="icon test-icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-btn-pic-closex-copy" />
            </svg>
            <svg
              v-if="getResultColor(option.index, idx) === 'success'"
              class="icon test-icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-ico-jkda-xzx" />
            </svg>
            <span>{{ option.index + 1 }}</span
            >. <span v-html="option.item" />
          </el-checkbox>
        </el-checkbox-group>
        <el-radio-group
          v-if="item.type === 'radio'"
          size="small"
          :disabled="true"
        >
          <el-radio
            v-for="(option, index) of item.standard_options"
            :key="index"
            :label="index"
            :class="getResultColor(index, idx)"
            @change="setOptions(index, idx, item._id)"
          >
            <svg
              v-if="getResultColor(option.index, idx) === 'error'"
              class="icon test-icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-btn-pic-closex-copy" />
            </svg>
            <svg
              v-if="getResultColor(option.index, idx) === 'success'"
              class="icon test-icon"
              aria-hidden="true"
            >
              <use xlink:href="#icon-ico-jkda-xzx" />
            </svg>
            <span>{{ index + 1 }}</span
            >. <span v-html="option.item" />
          </el-radio>
        </el-radio-group>
      </div>
    </template>
    <no-jurisdiction
      v-if="!questionList.length"
      title="你还没有错题，表现很棒哦！"
    />
  </div>
</template>

<script>
import { getErrorQuestion, delQuestion } from '@/api/study'
import NoJurisdiction from './NoJurisdiction'
export default {
  components: {
    NoJurisdiction
  },
  data() {
    return {
      questionList: []
    }
  },
  computed: {
    getResultColor() {
      return function(index, i) {
        index = parseInt(index)
        i = parseInt(i)
        for (const item of this.questionList[i].standard_answers) {
          if (parseInt(item) === index) {
            return 'success'
          }
        }
        for (const i of this.questionList[i].answers) {
          if (index === parseInt(i)) return 'error'
        }
        return ''
      }
    }
  },
  created() {
    this.getErrorQuestion()
  },
  methods: {
    async getErrorQuestion() {
      const { data, code } = await getErrorQuestion()
      if (code === 20000) {
        this.questionList = data.list
      }
    },
    review(index) {
      this.$router.push(
        '/study/course-detail/' + this.questionList[index].catalog
      )
    },
    async delQuestion(id) {
      const { data, code } = await delQuestion(id)
      if (code === 20000 && data.result) {
        this.$message({ message: '已通过', type: 'success' })
        this.getErrorQuestion()
      }
    }
  }
}
</script>

<style scoped lang="scss">
.wrong-questions {
  height: 100%;
  min-height: 100%;
  .title {
    margin: 20px;
    font-size: 18px;
    text-align: center;
    color: #17233d;
  }
  .question {
    margin: 0 auto 20px;
    padding: 30px;
    width: 1200px;
    border-radius: 4px;
    box-shadow: 0 0 4px 2px #f0f0f0;
    .stem {
      font-size: 14px;
      color: #576277;
      margin-bottom: 15px;
      .operation {
        float: right;
        .pass {
          color: #0085ff;
          cursor: pointer;
          .icon {
            width: 28px;
            height: 17px;
            vertical-align: sub;
          }
        }
        .review {
          margin-left: 10px;
          color: #f8747f;
          cursor: pointer;
          .icon {
            width: 28px;
            height: 17px;
            vertical-align: sub;
          }
        }
      }
    }
    .answer-item {
      margin-left: 8px;
      font-size: 14px;
      color: #576277;
      margin-bottom: 15px;
      .test-icon {
        display: inline-block;
        margin-right: 10px;
        width: 16px;
        height: 16px;
        vertical-align: middle;
      }
      .blank-icon {
        display: inline-block;
        margin-right: 8px;
        width: 16px;
        height: 16px;
        background: #fff;
        border: 1px solid #e8eaec;
        border-radius: 9px;
      }
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  /deep/.el-radio-group {
    margin-left: 10px;
    .success {
      p {
        color: #67c23a;
      }
      span {
        color: #67c23a !important;
      }
    }
    .error {
      p {
        color: #f56c6c;
      }
      span {
        color: #f56c6c !important;
      }
    }
    .el-radio:not(:last-child) {
      display: block;
      margin-bottom: 14px;
    }
  }
  /deep/.el-checkbox-group {
    margin-left: 10px;
    .success {
      p {
        color: #67c23a;
      }
      span {
        color: #67c23a !important;
      }
      .el-checkbox__inner {
        background-color: transparent;
        border: none;
      }
    }
    .error {
      p {
        color: #f56c6c;
      }
      span {
        color: #f56c6c !important;
      }
      .el-checkbox__inner {
        background-color: transparent;
        border: none;
      }
    }
    .el-checkbox:not(:last-child) {
      display: block;
      margin-bottom: 14px;
    }
  }
  .test-icon {
    position: absolute;
    left: 0;
    width: 15px;
    height: 15px;
  }
  /deep/.el-radio p {
    display: inline;
  }
  /deep/.el-radio__label {
    span {
      color: #606266;
    }
  }
  /deep/.el-checkbox__label {
    span {
      color: #606266;
      p {
        display: inline-block;
      }
    }
  }
}
</style>
